<template>
  <div>
    <img style="width:1174px;margin-bottom:15px" src="./report3pic.png" />
    <div id="div1" class="eDiv"></div>
  </div>
</template>
<script>
import currentLocate from "@/components/common/tools/currentLocate";
export default {
  data() {
    return {};
  },
  mounted: function() {
    var option1 = {
      title: {
        text: "广东机器人公司数量分布图",
        subtext: "数据来源：中国机器人网"
      },
      tooltip: {
        trigger: "axis"
      },
      legend: {
        data: ["数量"]
      },
      toolbox: {
        show: true,
        feature: {
          mark: { show: true },
          dataView: { show: true, readOnly: false },
          magicType: { show: true, type: ["line", "bar"] },
          restore: { show: true },
          saveAsImage: { show: true }
        }
      },
      calculable: true,
      xAxis: [
        {
          type: "category",
          data: [
            "江门市",
            "汕头市",
            "阳江市",
            "潮州市",
            "梅州市",
            "河源市",
            "云浮市",
            "肇庆市",
            "韶关市",
            "清远市",
            "揭阳市",
            "汕尾市",
            "珠海市",
            "湛江市",
            "茂名市",
            "中山市",
            "惠州市",
            "东莞市",
            "佛山市",
            "广州市",
            "深圳市"
          ]
        }
      ],
      yAxis: [
        {
          type: "value"
        }
      ],
      series: [
        {
          name: "数量",
          type: "bar",
          data: [
            11,
            8,
            0,
            2,
            0,
            2,
            0,
            6,
            0,
            3,
            0,
            0,
            32,
            0,
            1,
            46,
            17,
            616,
            138,
            475,
            1097
          ],
          markLine: {
            data: [{ type: "average", name: "平均值" }]
          }
        }
      ]
    };
    var myChart1 = this.echarts.init(document.getElementById("div1"));
    myChart1.setOption(option1);
  }
};
</script>


